const alt1 = "IXEDU poster";
const alt2 = "CMU EVAL poster";
const alt3 = "Art To Connect poster";
const alt4 = "Tech4Society poster";
const alt5 = "CMU Global Water Brigades poster";
const alt6 = "Green Shift poster";

const posters2022 = [
    { image: poster22_1, alt: "CMU Dining Service poster" },
    { image: poster22_2, alt: "Global Medical Brigades poster" },
    { image: poster22_3, alt: "Kindness First Penguin poster" },
    { image: poster22_4, alt: "Operation Better Block poster" },
    { image: poster22_5, alt: "Sustain CMU poster" },
    { image: poster22_6, alt: "Strong Women Strong Girls poster" },
    { image: poster22_7, alt: "Women Leaders of Science and Engineering poster" },
    { image: poster22_8, alt: "Wave: Connect, Launch, Impact poster" }
]

